@import "../../variables.scss";

// /* FRONT-END DISPLAY */

.wp-block-build-n-bloom-blocks-image-background[data-class*="user-display"] {
	@media screen and (min-width:$sml-screen-size) {
		.mobile-background-image {
			display: none;
		}
	}
	@media screen and (max-width:$sml-screen-size) {
		.background-image {
			display: none;
		}
	}

	.background-image {
		@for $i from 0 through 20 {
			&[data-container-height="#{$i * 5}"] {
				height: calc($i*5) + vh;
			}
		}
	}
	.mobile-background-image {
		@for $i from 0 through 20 {
			&[data-mobile-container-height="#{$i * 5}"] {
				height: calc($i*5) + vh;
			}
		}
	}
}

